label {
    padding: 24px 20px 15px 10px;
    display: inline-block;
    margin-left: 15px;
    display: inline-block;
    width: 220px;
}

input, select {
    background-color: white;
    border: 1px solid #1198cd;
    border-radius: 5px;
    color: #495057;

    width: 50%;
    padding: 10px 20px;
    box-sizing: border-box;
}

input[type="submit"] {
    width: 20%;
    float: right;
    margin-right: -495px;
    margin-top: 20px;
    display: flex;

    background-color: #4281a4;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    grid-area: paramsSubmit;
}
  
input[type=submit]:hover {
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    color: white;
    border-color: #2198ab;
    background-color: #1198cd;
    box-shadow: #495057;
}

